import axios from 'axios'
const apiKey = 'ti9f-GkjiobyTQRs_NJTSRMbPxWX7etn'
const PLANTS_API = `https://api.mlab.com/api/1/databases/igarden/collections/plants`
const LIBRARY_API = `https://api.mlab.com/api/1/databases/igarden/collections/library`
const symptomsID = '5b01acf660b4261e1badcdce'

export default {
  getPlants () {
    return axios.get(`${PLANTS_API}?apiKey=${apiKey}`)
      .then(response => response.data)
      .catch(error => error)
  },
  deletePlant (id) {
    return axios.delete(`${PLANTS_API}/${id}?apiKey=${apiKey}`)
      .then(response => response.data)
      .catch(error => error)
  },
  addPlant (plant) {
    return axios.post(`${PLANTS_API}?apiKey=${apiKey}`, plant)
      .then(response => response.data)
      .catch(error => error)
  },
  updatePlant (PLANT_ID, newPlant) {
    return axios.put(`${PLANTS_API}/${PLANT_ID}?apiKey=${apiKey}`, newPlant)
      .then(response => response.data)
      .catch(error => error)
  },
  getPlant (plant) {
    return axios.get(`${PLANTS_API}?apiKey=${apiKey}&q={"gr":"${plant}"}`)
      .then(response => response.data)
      .catch(error => error)
  },
  getLibrary () {
    return axios.get(`${LIBRARY_API}?apiKey=${apiKey}`)
      .then(response => response.data)
      .catch(error => error)
  },
  updateLibrary (data) {
    return axios.put(`${LIBRARY_API}/${symptomsID}?apiKey=${apiKey}`, data)
      .then(response => response.data)
      .catch(error => error)
  }
}



