<template>
  <v-card flat>
    <v-card-title class="pa-0">
      <span class="body-2">{{ title }}:</span>
      <v-spacer></v-spacer>
      <slot name="topAction"></slot>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="pa-0">
      <slot name="selection"></slot>
      <v-slide-x-reverse-transition>
        <v-card v-if="SELECTED.length > 0" flat class="pa-0">
          <v-list class="pa-0">
            <v-subheader :class="PREVIEW.color + ' lighten-5'">
              <span>Προεπισκόπηση</span>
              <v-spacer></v-spacer>
              <v-icon>{{ subIcon }}</v-icon>
            </v-subheader>
            <v-list-tile class="py-2">
              <v-list-tile-avatar><v-icon>library_books</v-icon></v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ subHeader }}:</v-list-tile-title>
                <v-list-tile-sub-title>{{ SELECTED.join(', ') }}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile :class="PREVIEW.color">
              <v-list-tile-avatar><v-icon dark>{{ PREVIEW.icon }}</v-icon></v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title class="body-2 white--text">{{ PREVIEW.message }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card>
      </v-slide-x-reverse-transition>
    </v-card-text>
    <v-slide-x-reverse-transition>
      <v-card-actions v-if="SELECTED.length > 0" :class="PREVIEW.color+' lighten-5'" class="pa-0">
        <slot name="bottomAction"></slot>
      </v-card-actions>
    </v-slide-x-reverse-transition>
  </v-card>
</template>

<script>
export default {
  name: 'StepContentCard',
  props: ['title', 'subHeader', 'subIcon', 'SELECTED', 'PREVIEW'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css" >
</style>



