import Vue from 'vue'
import VueRouter from 'vue-router'
import Landing from '@/components/main/Landing'
import Diagnosis from '@/components/Diagnosis'
import Edit from '@/components/Edit'
import PageNotFound from '@/components/main/PageNotFound'

Vue.use(VueRouter)

export default new VueRouter({
  routes: [
    {
      path: '/',
      name: 'Landing',
      component: Landing
    }, {
      path: '/diagnosis',
      name: 'PlantsCatalog',
      component: Diagnosis
    }, {
      path: '/diagnosis/:gr',
      name: 'SelectSymptom',
      component: Diagnosis
    }, {
      path: `/diagnosis/:gr/:disease`,
      name: 'DiseasePage',
      component: Diagnosis
    }, {
      path: '/edit',
      name: 'EditSearch',
      component: Edit
    }, {
      path: '/edit/:gr',
      name: 'EditPlant',
      component: Edit
    }, {
      path: '*',
      name: 'PageNotFound',
      component: PageNotFound
    }
  ]
})



