<template>
  <v-card class="my-4">
    <v-card-title :class="plant.vue_color || 'secondary'" class="title white--text pa-0">
      <v-avatar tile :color="plant.vue_color" size="50">
        <v-icon medium dark>local_hospital</v-icon>
      </v-avatar>
      <span v-if="diseaseNotExist">{{ plant.gr }}</span>
      <template v-else>
        <span class="white--text">{{ plant.gr }}</span>
        <v-icon :color="plant.vue_color + ' lighten-4'">keyboard_arrow_right</v-icon>
        <span class="white--text">{{ disease.name }}</span>
      </template>
    </v-card-title>
    <v-card-text class="pa-0">
      <v-list three-line v-if="!diseaseNotExist" class="pa-0">
        <template v-if="steps.length > 0">
          <v-subheader :class="plant.vue_color + ' lighten-5'">Κριτήρια στα οποία απαντήσατε</v-subheader>
          <ListGroup :color="plant.vue_color" :sub="`(<b>${totalValues}</b>) επιλεγμένες τιμές`" icon="library_books"
            :title="`(<b>${steps.length}</b>) απαντηθέντα κριτήρια`">
            <template slot="items" v-for="(step, s) in steps">
              <v-list-tile :key="`stepTile_${s}`">
                <v-list-tile-avatar><v-icon>format_list_bulleted</v-icon></v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    <b>{{ step.qualifier }}</b>
                    <v-icon>arrow_right</v-icon>
                    <span>{{ step.selectedValues }}</span>
                  </v-list-tile-title>
                  <v-list-tile-sub-title>Κριτήριο Νο.{{s+1}}</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider v-if="s < steps.length-1" :key="`stepDivider_${s}`"></v-divider>
            </template>
          </ListGroup>
        </template>
        <v-subheader :class="plant.vue_color + ' lighten-5'">Πληροφορίες ασθένειας</v-subheader>
        <template v-for="(item, i) in D_INFO">
          <v-tooltip bottom :key="`tooltip-${i}`">
            <DiseaseTile
              slot="activator"
              :handler="() => showDescription(item)"
              :color="plant.vue_color"
              :sub="item.sub"
              :icon="item.icon">
              <span v-html="item.title"></span>
            </DiseaseTile>
            <span>Πατήστε για εμφάνιση αναδυόμενου παραθύρου</span>
          </v-tooltip>
        </template>
        <ListGroup icon="security" :color="plant.vue_color" :title="TREATMENT_TITLE" sub="Καταπολέμηση">
          <template slot="items" v-for="(treatment, t) in disease.treatment">
            <v-list-tile :key="`tile-${t}`">
              <v-list-tile-avatar><v-icon>format_list_bulleted</v-icon></v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ treatment }}.</v-list-tile-title>
                <v-list-tile-sub-title>Νο.{{ t+1 }} μέτρο</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider v-if="t+1 < disease.treatment.length" :key="`d-${t}`"></v-divider>
          </template>
        </ListGroup>
        <template v-for="(symptom, s) in Object.keys(disease.rules)">
          <v-subheader :class="plant.vue_color+' lighten-5'" :key="`subHeader_${s}`">Το φυτό παρουσιάζει {{ symptom }}</v-subheader>
          <ListGroup :key="`group_${s}`" :title="`Κριτήρια ασθένειας με ${symptom}`" :color="plant.vue_color" icon="visibility"
            :sub="`(<b>${Object.keys(disease.rules[symptom]).length}</b>) συνολικά`">
            <template slot="items" v-for="(rule, r) in Object.keys(disease.rules[symptom])">
              <v-list-tile :key="`tile-${s}-${r}`">
                <v-list-tile-avatar><v-icon>notes</v-icon></v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>
                    <b>{{ rule }}</b>
                    <v-icon class="pb-1">arrow_right</v-icon>
                    <span>{{ disease.rules[symptom][rule] }}</span>
                  </v-list-tile-title>
                  <v-list-tile-sub-title>
                    <b>{{ disease.rules[symptom][rule].length }}</b>
                    <span v-if="disease.rules[symptom][rule].length == 1">εμφανιζόμενη τιμή</span>
                    <span v-else>εμφανιζόμενες τιμές</span>
                  </v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
              <v-divider :key="`divider-${s}-${r}`" v-if="r < Object.keys(disease.rules[symptom]).length-1" ></v-divider>
            </template>
          </ListGroup>
        </template>
      </v-list>
      <div v-else>
        <v-alert type="error" transition="slide-x-transition" :value="true" class="ma-0 subheading">
          Η ασθένεια <em><b>{{ routeDisease }}</b></em>, δεν βρέθηκε στο αρχείο του φυτού {{ plant.gr }}.<br>
          Για πλήρες αρχείο ασθενειών του κηπευτικού, μεταβείτε στη Σελίδα Διάγνωσης πατώντας στο κουμπί <v-icon color="white" class="pb-1">local_florist</v-icon>.
        </v-alert>
        <v-divider :class="plant.vue_color + ' lighten-2'"></v-divider>
        <v-btn class="ma-0" depressed block large color="white" @click="toDiagnosis">
          <v-icon large :color="plant.vue_color">local_florist</v-icon>
        </v-btn>
      </div>
    </v-card-text>
    <v-dialog persistent max-width="600px" transition="scale-transition" v-model="D_DIALOG.show">
      <DiseaseDialog :item="D_DIALOG.item" :plant="plant" :handler="hideDescription"></DiseaseDialog>
    </v-dialog>
  </v-card>
</template>

<script>
import LoadingPlants from './LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'
import DiseaseTile from '@/components/layouts/DiseaseTile'
import ListGroup from '@/components/layouts/ListGroup'
import DiseaseDialog from '@/components/layouts/DiseaseDialog'
import _ from 'lodash'

export default {
  name: 'Diseases',
  props: ['plant', 'routeDisease', 'steps'],
  components: {
    LoadingPlants,
    CardLayout,
    DiseaseTile,
    ListGroup,
    DiseaseDialog
  },
  data () {
    return {
      disease: null,
      selectedPlant: false,
      showTreatment: false,
      D_DIALOG: {
        show: false,
        item: {}
      }
    }
  },
  created () { this.disease = this.plant.diseases.find(d => d.name === this.routeDisease) },
  computed: {
    totalValues () {
      return _.flatMap(this.steps, _.property('selectedValues')).length
    },
    TREATMENT_TITLE () {
      var count = ''
      if (this.disease.treatment.length > 1) {
        count = 'μέτρα'
      } else {
        count = 'μέτρο'
      }
      return `${this.disease.treatment.length} ${count} αντιμετώπισης`
    },
    D_RULES () {
      var rules = []
      for (var symptom in this.disease.rules) {
        for (var rule in this.disease.rules[symptom]) {
          rules.push({
            rule: rule,
            values: this.disease.rules[symptom][rule]
          })
        }
      }
      return rules
    },
    D_INFO () {
      return [{
        icon: 'info',
        title: this.disease.name,
        sub: 'Όνομα ασθένειας'
      }, {
        icon: 'title',
        title: this.disease.parasitic ? 'Παρασιτική' : 'Μη παρασιτική',
        sub: 'Τύπος ασθένειας'
      }, {
        icon: 'bug_report',
        title: `${this.disease.parasitic ? `<em>${this.disease.pathogen}</em> | <b>${this.disease.pathogen_type || 'Μικροοργανισμός'}</b>`
          : this.disease.pathogen}`,
        sub: 'Παθογόνο αίτιο'
      }, {
        icon: 'description',
        title: this.disease.description,
        sub: 'Σύντομη περιγραφή'
      }]
    },
    diseaseNotExist () {
      return this.disease === undefined
    }
  },
  methods: {
    hideDescription () { this.D_DIALOG.show = false },
    toDiagnosis () { this.$router.push('/diagnosis') },
    showDescription (item) {
      item.disease = this.disease.name
      this.D_DIALOG = { show: true, item: item }
    }
  }
}
</script>

<style lang="css">
.nbr{
  border-radius: 0px;
}
.ntd{
  text-decoration: none;
}
</style>



