<template>
  <v-footer id="footer" class="light-green white--text pa-4 subheading">
    <span>{{ appTitle }}</span>
    <v-icon dark small class="mx-1">copyright</v-icon>
    <span>AUA | {{ year }}</span>
  </v-footer>
</template>

<script>
export default {
  name: 'AppFooter',
  props: ['appTitle'],
  data () {
    return {
      year: new Date().getFullYear()
    }
  }
}
</script>

<style lang="css" scoped>
</style>



