<template>
  <v-card id="SymptomsDialogCard">
    <v-card-media src="/static/fill/symptoms.jpg" height="300px"></v-card-media>
    <v-card-title class="d-block">
      <div class="headline">
        <v-badge :color="color">
          <span slot="badge">{{symptom.diseases.length}}</span>
          <span>{{ symptom.name }}</span>
        </v-badge>
      </div>
      <span class="grey--text">Περιγραφή συμπτώματος</span>
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text class="subheading pa-0">
      <div class="pa-3" v-html="symptom.description"></div>
      <v-list class="pa-0">
        <v-divider></v-divider>
        <v-subheader :class="color+' lighten-5'">{{symptom.diseases.length}} ασθένειες με {{symptom.name}} στο αρχείο</v-subheader>
        <v-divider></v-divider>
        <template v-for="(disease, d) in symptom.diseases">
          <v-list-tile :key="`diseaseTile-${d}`">
            <v-list-tile-avatar>
              <v-icon>local_hospital</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>{{ disease }}</v-list-tile-content>
          </v-list-tile>
          <v-divider v-if="d < symptom.diseases.length-1" inset :key="`diseaseDivider-${d}`"></v-divider>
        </template>
        <template v-if="symptom.types">
          <v-divider></v-divider>
          <v-subheader :class="color+' lighten-5'">Ενδεικτικοί τύποι συμπτώματος</v-subheader>
          <v-divider></v-divider>
          <template v-for="(type, t) in symptom.types">
            <v-list-tile :key="`typeTile-${t}`">
              <v-list-tile-avatar>
                <v-icon>format_list_bulleted</v-icon>
              </v-list-tile-avatar>
              <v-list-tile-content>{{ type }}</v-list-tile-content>
            </v-list-tile>
            <v-divider :inset="t < symptom.types.length-1" :key="`typeDivider-${t}`"></v-divider>
          </template>
        </template>
      </v-list>
    </v-card-text>
    <v-card-actions class="pa-0">
      <v-btn large block :color="color" flat class="ma-0" @click="handler">ΚΛΕΙΣΙΜΟ</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'SymptomsDialogCard',
  props: ['color', 'symptom', 'handler'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css">
.fwn{
  font-weight: normal;
}
.mediaOverlay{
  background-color: rgba(0,0,0,0.3);
}
</style>



