<template>
  <v-card id="loading" flat class="py-3 text-xs-center">
    <v-progress-circular :size="80" color="lime accent-2" :width="3" indeterminate></v-progress-circular>
  </v-card>
</template>

<script>
export default {
  name: 'LoadingPlants',
  props: ['plant'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css">
#loading{
  background-color: rgba(139,195,74,0.4);
}
</style>



