<template>
  <v-layout row class="mb-3">
    <v-flex sm12 md10 offset-md1>
      <v-card flat tile :style="`margin-top: -${height}px`" class="transparent pa-0">
        <v-toolbar card :height="height">
          <v-avatar tile :size="height" color="lime accent-4" class="mx-0">
            <v-icon medium color="white">{{ icon }}</v-icon>
          </v-avatar>
          <v-toolbar-title class="body-2 grey--text">{{ title }}</v-toolbar-title>
        </v-toolbar>
        <v-card-text class="px-0">
          <slot name="content"></slot>
        </v-card-text>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'CardLayout',
  props: ['icon', 'title', 'url'],
  data () {
    return {
      height: 42
    }
  }
}
</script>

<style lang="css">
</style>



