<template>
  <v-card class="my-4" id="selectSymptom">
    <v-card-title class="title white--text pa-0" :class="plant.vue_color || 'secondary'">
      <v-avatar tile :color="plant.vue_color  || 'secondary'" size="50">
        <v-icon medium dark>library_books</v-icon>
      </v-avatar>
      <span>{{ plant.gr || $route.params.gr }}</span>
    </v-card-title>
    <v-card-text class="pa-0" v-if="plant.diseases.length > 0">
      <v-stepper class="ma-0" vertical v-model="CURSOR">
        <v-stepper-step color="teal" :complete="CURSOR > 1" :step="1" class="subheading">
          <v-badge :color="plant.vue_color">
            <span v-if="SELECTED_SYMPTOMS.length > 0" slot="badge">{{SELECTED_SYMPTOMS.length}}</span>
            <span><v-icon class="pb-1">visibility</v-icon> Το φυτό παρουσιάζει :</span>
          </v-badge>
          <v-scale-transition>
            <small key="small" v-if="SELECTED_SYMPTOMS.length > 0">{{ SELECTED_SYMPTOMS.join(' / ') }}</small>
          </v-scale-transition>
        </v-stepper-step>
        <v-divider class="mb-2"></v-divider>
        <v-stepper-content :step="1">
          <StepContentCard title="Επιλέξτε συνδυασμό συμπτωμάτων" subIcon="visibility" subHeader="Το φυτό παρουσιάζει" :SELECTED="SELECTED_SYMPTOMS" :PREVIEW="PREVIEW">
            <template slot="topAction">
              <v-tooltip left>
                <v-btn slot="activator" icon flat><v-icon>help</v-icon></v-btn>
                <span>Επιλέξτε όποια συμπτώματα εμφανίζονται, σε ολόκληρο το φυτό</span>
              </v-tooltip>
            </template>
            <template slot="selection">
              <v-list two-line class="pa-0">
                <template v-for="(symptom, s) in SYMPTOMS">
                  <v-list-tile :key="`tile-${s}`">
                    <v-list-tile-avatar><v-icon>visibility</v-icon></v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title>
                        <v-checkbox @change="chooseSymptom" :label="symptom" :color="plant.vue_color" :value="symptom" v-model="SELECTED_SYMPTOMS">
                        </v-checkbox>
                      </v-list-tile-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-btn :loading="Object.keys(LIBRARY).length == 0" @click.stop="showDescription(symptom)" flat>ΠΕΡΙΓΡΑΦΗ</v-btn>
                    </v-list-tile-action>
                  </v-list-tile>
                  <v-divider v-if="s < SYMPTOMS.length-1" :key="`divider-${s}`" inset></v-divider>
                </template>
              </v-list>
            </template>
            <template slot="bottomAction">
              <v-btn flat icon :color="PREVIEW.color" @click="resetSymptoms"><v-icon>clear</v-icon></v-btn>
              <v-spacer></v-spacer>
              <v-btn flat large class="mx-0 nbr" :color="POSSIBLE_DISEASES.length == 1 ? 'success':'teal'" @click="diagnose"
                :disabled="POSSIBLE_DISEASES.length == 0">
                <v-badge :color="PREVIEW.color + ' lighten-2'">
                  <v-icon slot="badge" dark v-if="POSSIBLE_DISEASES.length == 1" small>done</v-icon>
                  <span slot="badge" v-else-if="POSSIBLE_DISEASES.length > 1">{{ POSSIBLE_DISEASES.length }}</span>
                  <span v-if="POSSIBLE_DISEASES.length !== 1">{{ PREVIEW.buttonText }}</span>
                  <span v-else>{{ POSSIBLE_DISEASES[0].name }}</span>
                </v-badge>
              </v-btn>
            </template>
          </StepContentCard>
        </v-stepper-content>
        <template v-if="POSSIBLE_DISEASES.length > 1" v-for="(step, s) in STEPS">
          <v-stepper-step class="subheading" color="teal" :complete="CURSOR > s+2" :key="`stepper-step_${s}`" :step="s + 2">
            <v-badge :color="plant.vue_color">
              <v-icon slot="badge" v-if="step.selectedValues.includes(FALSY_VALUE)" small dark>not_interested</v-icon>
              <span slot="badge" v-else-if="step.selectedValues.length > 0">{{ step.selectedValues.length }}</span>
              <span><v-icon class="pb-1">assignment</v-icon> {{ step.qualifier }} :</span>
            </v-badge>
            <v-scale-transition>
              <small key="small" v-if="step.selectedValues.length > 0">{{ step.selectedValues.join(' / ') }}</small>
            </v-scale-transition>
          </v-stepper-step>
          <v-stepper-content :key="`stepper-content_${s}`" :step="s+2">
            <StepContentCard title="Επιλέξτε όλες τις τιμές που παρατηρούνται, σε όλο το φυτό" subIcon="assignment" :subHeader="step.qualifier"
              :SELECTED="step.selectedValues"
              :PREVIEW="PREVIEW">
              <template slot="topAction">
                <v-tooltip left>
                  <v-btn slot="activator" icon flat><v-icon>help</v-icon></v-btn>
                  <span>Οι μη πιθανές επιλογές απενεργοποιούνται αυτόματα. Σε οποιοδήποτε σημείο, μπορείτε να
                  επανεκκινήσετε το σύστημα ή να κάνετε "Παράκαμψη βήματος".</span>
                </v-tooltip>
              </template>
              <template slot="selection">
                <v-list two-line class="pa-0">
                  <template v-for="(value, v) in step.values">
                    <v-list-tile :key="`value-${s}_${v}`">
                      <v-list-tile-avatar><v-icon>notes</v-icon></v-list-tile-avatar>
                      <v-list-tile-content>
                        <v-list-tile-title>
                          <v-checkbox class="value" @change="selectValue(step)" :label="value" :color="plant.vue_color" :value="value" v-model="V_BUFFER">
                          </v-checkbox>
                        </v-list-tile-title>
                      </v-list-tile-content>
                      <v-list-tile-action><v-btn flat @click.stop="showValueDialog(step, value)">ΛΕΠΤΟΜΕΡΕΙΕΣ</v-btn></v-list-tile-action>
                    </v-list-tile>
                    <v-divider :id="`divider_${value}`" v-if="s < step.values.length-1" :key="`divider-${s}_${v}`" inset></v-divider>
                  </template>
                  <v-list-tile>
                    <v-list-tile-avatar><v-icon>not_interested</v-icon></v-list-tile-avatar>
                    <v-list-tile-content>
                      <v-list-tile-title>Δεν παρατηρώ κάποια από αυτές τις επιλογές.</v-list-tile-title>
                      <v-list-tile-sub-title>
                        <v-switch class="ml-1" color="teal" label="Παράκαμψη βήματος" :value="FALSY_VALUE" v-model="V_BUFFER" @change="selectValue(step, FALSY_VALUE)"></v-switch>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-list-tile>
                </v-list>
              </template>
              <template slot="bottomAction">
                <v-btn flat icon @click="resetSystem" :color="PREVIEW.color"><v-icon>refresh</v-icon></v-btn>
                <v-btn flat icon @click="resetStep(step)" :color="PREVIEW.color"><v-icon>clear</v-icon></v-btn>
                <v-spacer></v-spacer>
                <v-btn flat large class="mx-0 nbr" :color="PREVIEW.color" @click="nextValueStep(step)" :disabled="V_BASED.length == 0">
                  <v-badge :color="PREVIEW.color + ' lighten-2'">
                    <v-icon slot="badge" dark v-if="V_BASED.length == 1" small>done</v-icon>
                    <span slot="badge" v-else-if="V_BASED.length > 1">{{ V_BASED.length }}</span>
                    <span v-if="V_BASED.length !== 1">ΕΠΟΜΕΝΟ</span>
                    <span v-else>{{ V_BASED[0].name }}</span>
                  </v-badge>
                </v-btn>
              </template>
            </StepContentCard>
          </v-stepper-content>
        </template>
      </v-stepper>
    </v-card-text>
    <v-card-text class="subheading pa-0" v-else>
      <v-alert type="error" :value="true" class="alert ma-0 pa-4 subheading">
        Καμία ασθένεια για το φυτό <b>{{ plant.gr }}</b> στη Βάση Γνώσεως. Μεταβείτε στη <router-link class="white--text" :to="`/edit/${plant.gr}`">Σελίδα επεξεργασίας</router-link> του κηπευτικού.
      </v-alert>
    </v-card-text>
    <!-- Symptom and Value Dialogs -->
    <v-dialog lazy persistent max-width="600px" transition="scale-transition" v-model="V_DIALOG.show">
      <ValueDialog :plant="plant" :step="V_DIALOG.step" :handler="hideValueDialog"></ValueDialog>
    </v-dialog>
    <v-dialog lazy persistent max-width="600px" v-if="LIBRARY" transition="scale-transition" v-model="D_DIALOG.show">
      <SymptomsDialog :color="plant.vue_color" :symptom="D_DIALOG.symptom" :handler="hideDescription"></SymptomsDialog>
    </v-dialog>
  </v-card>
</template>

<script>
import _ from 'lodash'
import swal from 'sweetalert2'
import I_E from '@/lib/Inference_Engine'
import API from '@/lib/API'
import SymptomsDialog from '@/components/layouts/SymptomsDialog'
import StepContentCard from '@/components/layouts/StepContentCard'
import ValueDialog from '@/components/layouts/ValueDialog'
import {bus} from '@/lib/bus'

export default {
  name: 'selectSymptom',
  props: ['sickPlant'],
  components: {
    SymptomsDialog,
    StepContentCard,
    ValueDialog
  },
  data () {
    return {
      plant: this.sickPlant,
      SELECTED_SYMPTOMS: [],
      LIBRARY: {},
      V_DIALOG: {
        show: false,
        step: {}
      },
      D_DIALOG: {
        show: false,
        symptom: {}
      },
      C_SYMPTOM: '',
      C_SYMPTOM_DISEASES: [],
      CURSOR: 1,
      COMPLETED_STEPS: [],
      V_BUFFER: [],
      POSSIBLE_DISEASES: [],
      STEPS: [],
      V_BASED: [],
      PREVIEW: {},
      FALSY_VALUE: 'Καμία επιλογή'
    }
  },
  created () {
    API.getLibrary().then(library => { this.LIBRARY = library[0].symptoms })
  },
  computed: {
    SYMPTOMS () { return _.uniq(_.flatMap(this.plant.diseases, d => _.keys(d.rules))) },
    I_O () {
      return (STEP_QUALIFIER, FALSY_PARAMETER = false) => ({
        QUALIFIER: STEP_QUALIFIER,
        SYMPTOMS: this.SELECTED_SYMPTOMS,
        VALUES: this.V_BUFFER,
        FALSY: FALSY_PARAMETER
      })
    },
    conclusionURL () { return `/diagnosis/${this.plant.gr}/${this.POSSIBLE_DISEASES[0].name}` },
    valueExists () {
      return function (value) {
        return this.POSSIBLE_DISEASES.filter(disease => JSON.stringify(disease).includes(value)).length > 0
      }
    }
  },
  methods: {
    hideDescription () { this.D_DIALOG.show = false },
    showDescription (symptom) {
      this.D_DIALOG.symptom = this.LIBRARY[symptom] || {
        name: symptom,
        description: `Καμία περιγραφή για το σύμπτωμα <b>${symptom}</b>.`
      }
      this.D_DIALOG.symptom.diseases = this.countSymptom(symptom)
      this.D_DIALOG.show = true
    },
    hideValueDialog () { this.V_DIALOG.show = false },
    showValueDialog (step, value) {
      this.V_DIALOG.step = {
        'qualifier': step.qualifier,
        'value': value,
        'diseases': this.countValue(step.qualifier, value)
      }
      this.V_DIALOG.show = true
    },
    chooseSymptom (symptom) {
      this.POSSIBLE_DISEASES = I_E.S_BASED(this.plant.diseases, this.SELECTED_SYMPTOMS)
      this.PREVIEW = I_E.PREVIEW(this.POSSIBLE_DISEASES)
      this.STEPS = I_E.STEPS(this.POSSIBLE_DISEASES, this.SELECTED_SYMPTOMS)
    },
    diagnose () {
      this.COMPLETED_STEPS.push({
        qualifier: 'Το φυτό παρουσιάζει',
        selectedValues: this.SELECTED_SYMPTOMS,
        values: this.SYMPTOMS
      })
      if (this.POSSIBLE_DISEASES.length === 1) {
        bus.$emit('conclusion', { done: true, steps: this.COMPLETED_STEPS })
        this.$router.push(this.conclusionURL)
      } else {
        this.CURSOR++
      }
    },
    nextValueStep (currentStep) {
      // Make the whole POSSIBLE_DISEASES array equal to current V_BASED array
      this.POSSIBLE_DISEASES = this.V_BASED
      this.COMPLETED_STEPS.push(currentStep)
      if (this.POSSIBLE_DISEASES.length === 1) {
        bus.$emit('conclusion', { done: true, steps: this.COMPLETED_STEPS })
        this.$router.push(this.conclusionURL)
      } else {
        if (this.CURSOR > this.STEPS.length) { // Finish Diagnosis Proccess
          return swal({
            type: 'error',
            title: 'Κανένα συμπέρασμα!',
            text: 'Διάγνωση χωρίς συμπέρασμα. Σύμφωνα με τα κριτήρια που επιλέξατε δεν φαίνεται να υπάρχει κάποιο πιθανό συμπέρασμα στο iGarden.',
            confirmButtonText: 'Επανεκκίνηση Συστήματος'
          }).then(ok => ok.value ? this.$router.go() : false)
        } else {
          this.CURSOR++
          this.V_BUFFER = []
        }
      }
      Array.from(document.getElementsByClassName('value')).forEach(valueCheckBox => {
        var value = valueCheckBox.children[0].innerHTML
        if (!this.valueExists(value)) {
          var divider = document.getElementById(`divider_${value}`)
          if (divider) { divider.style.display = 'none' }
          valueCheckBox.parentElement.parentElement.parentElement.style.display = 'none'
        }
      })
    },
    selectValue (step, isFalsy = false) {
      if (isFalsy) {
        this.V_BUFFER = step.selectedValues = [this.FALSY_VALUE]
      } else {
        step.selectedValues = _.pull(this.V_BUFFER, this.FALSY_VALUE)
      }
      this.V_BASED = I_E.V_BASED(this.POSSIBLE_DISEASES, this.I_O(step.qualifier, isFalsy))
      this.PREVIEW = I_E.PREVIEW(this.V_BASED)
    },
    resetSymptoms () { this.SELECTED_SYMPTOMS = [] },
    resetStep (step) { this.V_BUFFER = step.selectedValues = [] },
    resetSystem () {
      swal({
        type: 'warning',
        title: 'Προσοχή!',
        html: 'Θέλετε σίγουρα να επανεκκινήσετε το σύστημα?<br>Όλες οι επιλογές σας θα χαθούν...',
        showCancelButton: true,
        cancelButtonText: 'Άκυρο',
        confirmButtonText: 'Επανεκκίνηση'
      }).then(ok => ok.value ? this.$router.go() : false)
    },
    countValue (qualifier, value) {
      var possibleOnValue = []
      this.plant.diseases.filter(disease => {
        this.SELECTED_SYMPTOMS.forEach(symptom => {
          if (disease.rules[symptom] && disease.rules[symptom][qualifier] && disease.rules[symptom][qualifier].includes(value)) {
            possibleOnValue.push(disease)
          }
        })
      })
      return _.flatMap(possibleOnValue, _.property('name'))
    },
    countSymptom (symptom) {
      var possibleOnSymptom = this.plant.diseases.filter(disease => _.keys(disease.rules).includes(symptom))
      return _.flatMap(possibleOnSymptom, _.property('name'))
    }
  }
}
</script>

<style lang="css">
.ntt{
  text-transform: none;
}
.nbr{
  border-radius: 0px;
}
.alert{
  border: none;
}
.chip{
  margin: 1px;
}
.cup{
  cursor: pointer;
}
</style>



