<template>
  <div id="EditPlantPage" class="pa-0">
    <transition-group name="scale-transition">
      <v-card key="PlantCard" v-if="plant">
        <v-card-title class="title white--text pa-0" :class="plant.vue_color || 'secondary'">
          <v-avatar tile :color="plant.vue_color  || 'secondary'" size="50">
            <v-icon medium dark>edit</v-icon>
          </v-avatar>
          <span>{{ plant.gr || $route.params.gr }}</span>
          <v-spacer></v-spacer>
          <v-tooltip class="ma-0" left>
            <v-btn slot="activator" dark flat icon @click="logout">
              <v-icon medium>exit_to_app</v-icon>
            </v-btn>
            <span>Αποσύνδεση</span>
          </v-tooltip>
        </v-card-title>
        <v-card-text class="pa-0">
          <v-list two-line class="pa-0">
            <v-divider></v-divider>
            <v-subheader :class="plant.vue_color+' lighten-5'">Ασθένειες</v-subheader>
            <v-divider></v-divider>
            <template v-if="plant.diseases.length > 0">
              <v-list-tile v-for="(disease, d) in plant.diseases" :key="`tile_${d}`">
                <v-text-field
                  :color="plant.vue_color"
                  label="Ονομασία ασθένειας"
                  :placeholder="disease.name"
                  hint="Διορθώστε το όνομα της ασθένειας"
                  v-model="plant.diseases[d].name"
                  append-icon="delete"
                  :append-icon-cb="() => removeDisease(d)"
                  prepend-icon="local_hospital"
                  :prepend-icon-cb="() => openDiseaseDialog(plant.diseases[d])"
                  :rules="notEmpty"></v-text-field>
              </v-list-tile>
            </template>
            <v-list-tile v-else>
              <v-list-tile-avatar><v-icon>not_interested</v-icon></v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>Τίποτα για εμφάνιση.</v-list-tile-title>
                <v-list-tile-sub-title>Καμία ασθένεια στο αρχείο προσωρινά. Ξεκινήστε προσθέτοντας μια.</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider></v-divider>
            <v-subheader :class="plant.vue_color+' lighten-5'">
              <span>Προσθέστε νέα ασθένεια</span>
            </v-subheader>
            <v-divider></v-divider>
            <v-form ref="newDisease">
              <v-list-tile v-for="(diseaseField, df) in diseaseFields" :key="`newDiseaseTile_${df}`">
                <v-text-field
                  :model="diseaseField.model"
                  :color="plant.vue_color"
                  :label="diseaseField.label"
                  :prepend-icon="diseaseField.icon"
                  v-model="newDisease[diseaseField.model]"
                  :rules="notEmpty"></v-text-field>
              </v-list-tile>
              <v-list-tile>
                <v-switch
                  false-value
                  prepend-icon="title"
                  label="Παρασιτική ασθένεια"
                  :value="true"
                  success
                  model="parasitic"
                  :color="plant.vue_color"
                  v-model="newDisease.parasitic"></v-switch>
              </v-list-tile>
              <v-list-tile class="pb-1">
                <v-select
                  @input="addTreatment"
                  single-line
                  v-model="newDisease.treatment"
                  label="Καταπολέμηση ασθένειας"
                  hint="Πληκτρολογήστε το μέτρο που επιθυμείτε και πατήστε Enter."
                  chips
                  model="treatment"
                  prepend-icon="security"
                  :color="plant.vue_color"
                  tags></v-select>
              </v-list-tile>
              <v-list-tile>
                <v-select
                  hint="Πληκτρολογήστε το σύμπτωμα που επιθυμείτε και πατήστε Enter."
                  @input="addSymptom"
                  single-line
                  model="rules"
                  v-model="diseaseSymptoms"
                  label="Συμπτώματα ασθένειας"
                  chips
                  prepend-icon="visibility"
                  :color="plant.vue_color"
                  tags></v-select>
              </v-list-tile>
              <!-- <template v-for="(symptom, s) in newDisease.rules">
                <v-subheader :key="`symptomSubheader_${s}`" class="blue-grey lighten-5">Κριτήρια ασθένειας για {{ symptom }}</v-subheader>
                <template v-for="(rule, r) in newDisease.rules[symptom]">
                  <v-list-tile  :key="`ruleTile_${s}_${r}`">
                    <v-list-tile-content>
                      <v-text-field :color="plant.vue_color" label="Πρόταση" v-model="rule.rule"></v-text-field>
                    </v-list-tile-content>
                    <v-list-tile-action v-if="diseaseRules.length > 1">
                      <v-btn icon flat @click="removeRule(r)"><v-icon>delete</v-icon></v-btn>
                    </v-list-tile-action>
                  </v-list-tile>
                  <v-list-tile :key="`valuesTile_${s}_${r}`">
                    <v-list-tile-content>
                      <v-select :color="plant.vue_color" tags v-model="rule.values" label="Τιμές"></v-select>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <v-btn v-if="r+1 == diseaseRules.length" icon flat @click="addRule"><v-icon>add</v-icon></v-btn>
                    </v-list-tile-action>
                  </v-list-tile>
                </template>
              </template> -->
            </v-form>
          </v-list>
        </v-card-text>
        <v-card-actions>
          <v-btn flat fab color="green darken-1" @click="save" :loading="saving"><v-icon medium>save</v-icon></v-btn>
          <v-spacer></v-spacer>
          <v-btn flat fab color="red darken-1" @click="removePlant()" :loading="deleting"><v-icon medium>delete</v-icon></v-btn>
        </v-card-actions>
      </v-card>
      <v-card key="ErrorCard" v-else class="my-4">
        <v-card-title class="title white--text pa-0 secondary">
          <v-avatar tile color="white" size="55" class="mr-3">
            <v-icon medium>error</v-icon>
          </v-avatar>
          <span>Σφάλμα αναζήτησης</span>
        </v-card-title>
        <v-divider></v-divider>
        <v-card-text class="pa-0">
          <v-alert type="error" class="ma-0 pa-4 subheading" :value="true">
            Το κηπευτικό <b>{{ $route.params.gr }}</b> δεν βρέθηκε. Διορθώστε τη διεύθυνση <b>URL</b> ή μεταβείτε στη σελίδα <router-link class="white--text" to="/diagnosis">Διάγνωσης</router-link> για πλήρη κατάλογο των κηπευτικών του iGarden.
          </v-alert>
        </v-card-text>
      </v-card>
    </transition-group>
    <v-dialog hide-overlay lazy persistent fullscreen transition="dialog-bottom-transition" v-model="DIALOG.show">
      <EditDiseaseDialog :color="plant.vue_color" :disease="DIALOG.disease" :handler="() => { DIALOG.show = false }"></EditDiseaseDialog>
    </v-dialog>
  </div>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
import EditDiseaseDialog from '@/components/layouts/EditDiseaseDialog'
// import _ from 'lodash'
export default {
  name: 'EditPlantPage',
  props: ['chosenPlant', 'retrievedPlants'],
  components: {
    EditDiseaseDialog
  },
  data () {
    return {
      plant: {},
      plants: this.retrievedPlants,
      newDisease: {},
      deleting: false,
      saving: false,
      notEmpty: [
        v => !!v || 'Απαιτούμενο πεδίο!'
      ],
      diseaseSymptoms: [],
      diseaseRules: [{
        rule: '',
        values: []
      }],
      diseaseFields: [{
        label: 'Ονομασία ασθένειας',
        icon: 'info',
        model: 'name'
      }, {
        label: 'Παθογόνο ασθένειας',
        icon: 'bug_report',
        model: 'pathogen'
      }, {
        label: 'Περιγραφή ασθένειας',
        icon: 'description',
        model: 'description'
      }],
      DIALOG: {
        show: false,
        disease: {}
      }
    }
  },
  created () {
    this.plant = this.plants.find(p => p.gr === this.chosenPlant)
  },
  mounted () {},
  computed: {
    printDate: function () {
      var date = new Date()
      return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`
    },
    PLANT_ID () { return this.plants.find(p => p.gr === this.plant.gr)._id.$oid }
  },
  methods: {
    removePlant () {
      swal({
        type: 'warning',
        title: 'Προσοχή!',
        html: `Πρόκειται να διαγράψετε το φυτό <b>${this.plant.gr}</b> οριστικά!<br>Θέλετε σίγουρα να προχωρήσετε;`,
        showCancelButton: true,
        confirmButtonText: 'Διαγραφή',
        cancelButtonText: 'Άκυρο'
      }).then(willDelete => {
        this.deleting = true
        if (willDelete.value) {
          API.deletePlant(this.plant._id.$oid).then(deletedPlant => {
            this.deleting = false
            swal({
              type: 'success',
              title: 'Επιτυχής διαγραφή!',
              html: `Το φυτό <b>${deletedPlant.gr}</b> αφαιρέθηκε επιτυχώς από το αρχείο! Πατώντας <b>ΟΚ</b> θα μεταφερθείτε αυτόματα στη σελίδα διάγνωσης.`
            }).then(ok => this.$router.replace('/diagnosis'))
          })
        } else { this.deleting = false }
      })
    },
    addRule () {
      this.diseaseRules.push({
        rule: '',
        values: []
      })
    },
    removeDisease (index) {
      swal({
        type: 'warning',
        title: 'Προσοχή!',
        html: `Η ασθένεια ${this.plant.diseases[index].name} πρόκειται να διαγραφεί από το αρχείο!<br>Θέλετε σίγουρα να προχωρήσετε;`,
        showCancelButton: true,
        confirmButtonText: 'Διαγραφή',
        cancelButtonText: 'Άκυρο'
      }).then(ok => ok.value ? this.plant.diseases.splice(index, 1) : false)
    },
    removeRule (index) { this.diseaseRules.splice(index, 1) },
    addTreatment () {},
    addSymptom () {},
    save () {
      this.saving = true
      var emptyFields = 0
      this.$refs.newDisease.$children.forEach(field => {
        var e = field.$children[0]
        var key = e.$attrs['model']
        if (e.value !== undefined && e.value.length > 0 && key !== 'rules') { this.newDisease[key] = e.value }
        if (e.value === undefined || e.value.length === 0) { emptyFields++ }
      })
      this.newDisease.rules = {}
      this.diseaseSymptoms.forEach(symptom => { this.newDisease.rules[symptom] = {} })
      if (this.newDisease.parasitic === null) { this.newDisease.parasitic = false }
      if (emptyFields === 0) {
        this.plant.diseases.push(this.newDisease)
        this.updatePlant()
      } else {
        swal({
          type: 'warning',
          title: 'Κενά πεδία!',
          text: 'Συνεχίζοντας, δεν θα προστεθεί νέα ασθένεια απλώς θα γίνει αποθήκευση αλλαγών.',
          showCancelButton: true,
          confirmButtonText: 'Συνέχεια',
          cancelButtonText: 'Άκυρο'
        }).then(ok => { ok.value ? this.updatePlant() : this.saving = false })
      }
    },
    updatePlant () {
      API.updatePlant(this.PLANT_ID, this.plant).then(plant => {
        this.saving = false
        this.swalSuccess(plant.gr)
      })
    },
    logout () {
      return swal({
        type: 'warning',
        text: 'Θέλετε σίγουρα να αποσυνδεθείτε;',
        showCancelButton: true,
        confirmButtonText: ['Έξοδος'],
        cancelButtonText: ['Όχι'],
        confirmButtonColor: '#E53935'
      }).then(ok => {
        if (ok.value) {
          sessionStorage.removeItem('admin')
          this.$emit('logout')
          this.$router.go()
        }
      })
    },
    swalSuccess (plant) {
      swal({
        type: 'success',
        title: 'Επιτυχής αποθήκευση!',
        html: `Το φυτό ${plant} ανανεώθηκε επιτυχώς!`
      }).then(ok => this.$router.go())
    },
    openDiseaseDialog (disease) {
      this.DIALOG.disease = disease
      this.DIALOG.show = true
    }
  }
}
</script>

<style lang="css">
.whitePanel{
  background-color: rgba(255,255,255,0.55);
}
</style>



