<template>
  <div id="UpdateLibrary" class="pa-0">
    <v-card v-if="symptoms !== null" class="grey lighten-5">
      <v-card-text class="pa-0">
        <v-list v-if="symptoms !== null" two-line class="pa-0 ma-0 grey lighten-5">
          <v-subheader class="subheading lime accent-4 justify-center">
            <v-badge color="light-green darken-1">
              <span slot="badge">{{ keys(symptoms).length }}</span>
              <span>{{ subHeader }}</span>
            </v-badge>
          </v-subheader>
          <v-divider class="light-green"></v-divider>
          <template v-for="(symptom, s) in keys(symptoms)">
            <v-divider :key="`topDivider-${s}`"></v-divider>
            <v-subheader class="light-green lighten-5" :key="`subheader-${s}`">
              {{ symptoms[symptom].gender }} {{ symptoms[symptom].name }} αποτελεί:
            </v-subheader>
            <v-divider :key="`bottomDivider-${s}`"></v-divider>
            <v-list-tile :key="`tile-${s}`">
              <v-list-tile-content>
                <v-list-tile-sub-title>
                  <v-text-field
                    color="light-green"
                    :label="`Περιγραφή για ${symptom}`"
                    prepend-icon="visibility"
                    placeholder="Συμπληρώστε περιγραφή..."
                    v-model="symptoms[symptom].description"
                    :value="symptoms[symptom].description"
                    :key="`text-field-${s}`"
                    :rules="required"
                    required
                    @input="updateChangeState(symptom)"
                    hint="Συμπληρώστε μια περιληπτική περιγραφή του συμπτώματος."
                    :loading="saving">
                  </v-text-field>
                </v-list-tile-sub-title>
              </v-list-tile-content>
              <v-list-tile-action>
                <v-tooltip top>
                  <v-btn icon slot="activator">
                    <v-icon>{{ symptoms[symptom].changeDate ? 'update':'info'}}</v-icon>
                  </v-btn>
                  <span v-if="!symptoms[symptom].changeDate">Καμία επεξεργασία</span>
                  <span v-else>Επεξεργάσθηκε: {{ symptoms[symptom].changeDate }}</span>
                </v-tooltip>
              </v-list-tile-action>
            </v-list-tile>
          </template>
        </v-list>
      </v-card-text>
      <v-card-actions class="justify-center">
        <v-btn :loading="saving" flat fab @click="updateSymptoms" color="green darken-1">
          <v-icon medium>save</v-icon>
        </v-btn>
      </v-card-actions>
    </v-card>
    <LoadingPlants v-else></LoadingPlants>
  </div>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
import LoadingPlants from '@/components/LoadingPlants'
export default {
  name: 'UpdateLibrary',
  props: ['retrievedPlants', 'subHeader'],
  components: {
    LoadingPlants
  },
  data () {
    return {
      plants: this.retrievedPlants,
      symptoms: null,
      saving: false,
      required: [
        v => !!v || 'Απαιτούμενο πεδίο!'
      ]
    }
  },
  created () {
    API.getLibrary().then(library => {
      this.symptoms = library[0].symptoms
      for (let symptom in this.symptoms) { this.symptoms[symptom].changed = false }
    })
  },
  computed: {
    printDate: function () {
      var date = new Date()
      return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`
    }
  },
  methods: {
    updateSymptoms () {
      this.saving = true
      var newSymptoms = {}
      var changedCounter = 0
      newSymptoms.symptoms = this.symptoms
      for (let symptom in newSymptoms.symptoms) {
        if (this.symptoms[symptom].description.length === 0) {
          this.saving = false
          return swal({
            type: 'error',
            title: 'Κενό πεδίο!',
            html: 'Δεν επιτρέπεται η χρήση κενών πεδίων. Προσπαθήστε ξανά!'
          })
        }
        // Update Symptom ChangeDate
        if (this.symptoms[symptom].changed) {
          changedCounter++
          this.symptoms[symptom].changeDate = this.printDate
        }
      }
      if (changedCounter === 0) {
        this.saving = false
        return swal({
          type: 'warning',
          title: 'Καμία αλλαγή!',
          html: 'Δεν ανιχνεύτηκε κάποια αλλαγή για αποθήκευση.'
        })
      }
      API.updateLibrary(newSymptoms).then(response => {
        this.saving = false
        swal({
          type: 'success',
          title: 'Επιτυχής Αλλαγή!',
          text: 'Οι αλλαγές σας αποθηκεύτηκαν επιτυχώς.'
        }).then(ok => {
          this.$router.replace('/diagnosis')
        })
      })
    },
    updateChangeState (symptom) {
      this.symptoms[symptom].changed = true
    },
    keys (object) {
      return Object.keys(object)
    }
  }
}
</script>

<style lang="css">
</style>



