<template>
  <div>
    <v-navigation-drawer app temporary v-model="drawer" absolute dark class="light-green">
      <v-list two-line class="pa-0">
        <v-list-tile class="light-green darken-3 elevation-1">
          <v-list-tile-action><v-icon color="white" medium>local_florist</v-icon></v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title class="subheading">{{ appTitle }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <template v-for="(anchor,i) in navLinks">
          <v-list-tile
            :key="i"
            class="light-green"
            :to="anchor.url"
            active-class="lime accent-4">
            <v-list-tile-action><v-icon medium>{{anchor.icon}}</v-icon></v-list-tile-action>
            <v-list-tile-content>{{ anchor.title }}</v-list-tile-content>
          </v-list-tile>
          <v-divider v-if="i+1 < navLinks.length" class="light-green lighten-2" :key="`divider-${i}`"></v-divider>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-layout row class="light-green elevation-1">
      <v-flex sm12 md10 offset-md1>
        <v-toolbar height="55px" extension-height="42px" flat extended color="transparent white--text pa-0">
          <v-toolbar-side-icon class="hidden-md-and-up" dark @click="drawer = true"></v-toolbar-side-icon>
          <v-spacer class="hidden-md-and-up"></v-spacer>
          <v-toolbar-title class="headline mx-0 pt-1">
            <v-avatar size="34" color="white" class="mb-1 mr-1">
              <v-icon color="light-green">local_florist</v-icon>
            </v-avatar>
            <span class="hidden-sm-and-down">{{ appTitle }}</span>
          </v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items class="hidden-sm-and-down mx-0">
            <v-btn dark flat active-class="a-active" class="nbr"
              v-for="(anchor, i) in navLinks"
              :key="i"
              :to="anchor.url"
              :exact="anchor.home">
              <v-icon medium>{{ anchor.icon }}</v-icon>
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  name: 'AppHeader',
  props: ['appTitle'],
  data () {
    return {
      drawer: null,
      navLinks: [
        {
          url: '/',
          icon: 'home',
          title: 'ΑΡΧΙΚΗ',
          home: true
        }, {
          url: '/diagnosis',
          icon: 'library_books',
          title: 'ΔΙΑΓΝΩΣΗ'
        }, {
          url: '/edit',
          icon: 'edit',
          title: 'ΕΠΕΞΕΡΓΑΣΙΑ'
        }
      ]
    }
  }
}
</script>

<style lang="css" scoped>
.a-active{
  background-color: #AEEA00 !important;
}
.nbr{
  border-radius: 0px;
}
.fwn{
  font-weight: normal;
}
</style>



