<template>
  <v-card tile>
    <v-card-title class="white--text" :class="color || 'secondary'">
      <v-avatar tile :color="color || 'secondary'" size="30" class="mr-2">
        <v-icon medium dark>local_hospital</v-icon>
      </v-avatar>
      <span class="title">{{ disease.name }}</span>
      <v-spacer></v-spacer>
      <span>Κανόνες ασθένειας</span>
    </v-card-title>
    <v-card-text class="pa-0">
      <v-list class="pa-0">
        <template v-for="(symptom, s) in Object.keys(disease.rules)">
          <v-subheader :class="color+' lighten-5'" :key="`sub_${s}`">Το φυτό παρουσιάζει {{symptom}}</v-subheader>
          <v-divider :key="`divider_${s}`"></v-divider>
          <template v-for="(rule, r) in Object.keys(disease.rules[symptom])">
            <v-list-tile :key="`${s}_${r}`">
              <v-list-tile-avatar><v-icon>format_list_bulleted</v-icon></v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ rule }}<v-icon>arrow_right</v-icon>{{ disease.rules[symptom][rule] }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider inset :key="`divider_${s}_${r}`"></v-divider>
          </template>
        </template>
      </v-list>
    </v-card-text>
    <v-card-actions class="pa-0">
      <v-btn large block :color="color" flat class="ma-0" @click="handler">ΚΛΕΙΣΙΜΟ</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'EditDiseaseDialog',
  props: ['color', 'disease', 'handler'],
  data () {
    return {}
  }
}
</script>

<style lang="css">
</style>



