<template>
  <div id="DeletePlant" class="pa-0">
    <div v-if="loading" class="text-xs-center pa-3 whitePanel">
      <h1 class="subheading grey--text text--darken-1 text-xs-center">Αναμονή για επιβεβαίωση...</h1>
      <v-progress-linear color="light-green" :indeterminate="true"></v-progress-linear>
    </div>
    <div v-else>
      <slot name="title"></slot>
      <v-list two-line class="pa-0">
        <v-subheader class="subheading lime accent-4 justify-center">
          <v-badge color="light-green darken-1">
            <span slot="badge">{{ plants.length }}</span>
            <span>{{ subHeader }}</span>
          </v-badge>
        </v-subheader>
        <v-divider class="white"></v-divider>
        <template v-for="(plant, i) in plants">
          <v-list-tile :key="`tile_${i}`" :class="plant.vue_color || 'secondary'" class="white--text lighten-1">
            <v-list-tile-avatar>
              <v-icon large color="white">local_florist</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{ plant.gr }}</v-list-tile-title>
              <v-list-tile-sub-title>
                <v-chip label color="white" :text-color="plant.vue_color" class="text--darken-2 mx-0" small>
                  <em>{{ plant.latin }}</em>
                </v-chip>
              </v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-btn :loading="deleting" fab class="ma-0" flat color="white" @click="removePlant(plant.gr)">
                <v-icon medium class="darken-2">delete_forever</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
          <v-divider class="white" :key="`divider_${i}`" v-if="i+1 !== plants.length"></v-divider>
        </template>
      </v-list>
    </div>
  </div>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
export default {
  name: 'DeletePlant',
  props: ['retrievedPlants', 'subHeader'],
  data () {
    return {
      plants: this.retrievedPlants,
      loading: false,
      deleting: false
    }
  },
  mounted () {
  },
  methods: {
    removePlant (removedPlant) {
      swal({
        type: 'warning',
        title: 'Προσοχή!',
        html: `Πρόκειται να διαγράψετε το φυτό <b>${removedPlant}</b> οριστικά!\nΘέλετε σίγουρα να προχωρήσετε;`,
        showCancelButton: true,
        confirmButtonText: 'Διαγραφή',
        confirmButtonColor: 'red',
        cancelButtonText: 'Ακύρωση',
        reverseButtons: true
      }).then(ok => {
        this.deleting = true
        if (ok.value) {
          var id = this.plants.filter(plant => plant.gr === removedPlant)[0]._id.$oid
          API.deletePlant(id).then(deletedPlant => {
            this.plants = this.retrievedPlants.filter(plant => plant.gr !== removedPlant)
            this.deleting = false
            swal({
              type: 'success',
              title: 'Επιτυχής διαγραφή!',
              html: `Το φυτό <b>${deletedPlant.gr}</b> αφαιρέθηκε επιτυχώς από το αρχείο! Πατώντας <b>ΟΚ</b> θα μεταφερθείτε αυτόματα στη σελίδα διάγνωσης.`
            }).then(ok => this.$router.replace('/diagnosis'))
          })
        } else { this.deleting = false }
      })
    }
  }
}
</script>

<style lang="css" scoped>
.whitePanel{
  background-color: rgba(255,255,255, 0.8)
}
</style>



