<template>
  <CardLayout icon="library_books" title="Διάγνωση" url="/diagnosis">
    <template slot="content">
      <v-card v-if="plants !== null" class="transparent my-4" flat>
        <v-card-title class="pa-0 mb-3 d-block">
          <v-alert class="ma-0 alert subheading" color="light-green" :value="routePlantNull" type="info" transition="fade-transition">
            Με την Κοινή Ονομασία κάθε κηπευτικού, μεταφέρεστε στον αντίστοιχο σύνδεσμο του <b>Wikipedia</b>, ενώ κάθε βέλος σας μεταφέρει στη σελίδα της αντίστοιχης δραστηριότητας. Για περαιτέρω επεξεργασία της βάσης γνώσεως, μεταβείτε στη σελίδα Επεξεργασίας.
          </v-alert>
          <v-alert class="ma-0 alert subheading" :value="plantNotExist" type="error" transition="fade-transition">
            Το φυτό <b>{{ $route.params.gr }}</b> δεν βρέθηκε.<br>Διορθώστε την διεύθυνση <b>URL</b> και προσπαθήστε ξανά, αλλιώς ακολουθήστε τον Οδηγό Διάγνωσης για κάποιο από τα παρακάτω κηπευτικά.
          </v-alert>
        </v-card-title>
        <v-card-text class="pa-0" v-if="!plantNotExist && !routePlantNull">
          <transition-group name="slide-x-transition">
            <Conclusion :info="info" key="Conclusion" :steps="steps" :plant="sickPlant" v-if="conclusion" :routeDisease="$route.params.disease"></Conclusion>
            <SelectSymptom key="SelectSymptom" v-else :sickPlant="sickPlant"></SelectSymptom>
          </transition-group>
        </v-card-text>
        <v-card-text v-else class="pa-0">
          <v-slide-x-transition>
            <PlantsCatalog :plants="plants"></PlantsCatalog>
          </v-slide-x-transition>
        </v-card-text>
      </v-card>
      <LoadingPlants class="my-4" v-else></LoadingPlants>
    </template>
  </CardLayout>
</template>

<script>
import API from '@/lib/API'
import SelectSymptom from '@/components/SelectSymptom'
import PlantsCatalog from '@/components/PlantsCatalog'
import LoadingPlants from '@/components/LoadingPlants'
import CardLayout from '@/components/layouts/CardLayout'
import Conclusion from '@/components/Conclusion'
import {bus} from '@/lib/bus'

export default {
  name: 'Plants',
  components: {
    SelectSymptom,
    PlantsCatalog,
    LoadingPlants,
    CardLayout,
    Conclusion
  },
  data () {
    return {
      plants: null,
      info: true,
      steps: []
    }
  },
  created () {
    API.getPlants().then(p => { this.plants = p }).catch(error => console.log(error))
    bus.$on('conclusion', conclusion => {
      this.steps = conclusion.done ? conclusion.steps : []
    })
  },
  computed: {
    sickPlant: function () {
      return this.plants.find(p => p.gr === this.$route.params.gr)
    },
    routePlantNull () {
      return (this.$route.params.gr === undefined)
    },
    plantNotExist: function () {
      return !this.routePlantNull && this.sickPlant === undefined
    },
    conclusion () {
      return this.$route.params.disease !== undefined
    }
  }
}
</script>

<style lang="css">
.ntt {
  text-transform: none;
}
.nbr{
  border-radius: 0px;
}
.fwn{
  font-weight: normal;
}
.alert{
  border: none;
}
</style>



