<template>
  <CardLayout icon="edit" title="Επεξεργασία" url="/edit">
    <template slot="content">
      <AdminEdit v-if="admin" v-on:logout="admin = false"></AdminEdit>
      <v-card class="my-4" v-else>
        <v-alert transition="slide-x-transition" icon="vpn_key" class="ma-0" color="light-green" :value="true">
          <span class="subheading grey-text text--darken-2">
            Απαιτούνται δικαιώματα διαχειριστή για επεξεργασία της βάσης γνώσεως.
          </span>
        </v-alert>
        <v-card-text class="pa-0">
          <v-form ref="userForm">
            <v-list two-line class="pa-0">
              <v-list-tile>
                <v-list-tile-content>
                  <v-text-field
                    @keyup.enter="connect"
                    label="Όνομα Χρήστη"
                    type="text"
                    v-model="username"
                    single-line
                    color="light-green"
                    prepend-icon="person"
                    hint="Όνομα διαχειριστή με λατινικούς χαρακτήρες."></v-text-field>
                </v-list-tile-content>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-content>
                  <v-text-field
                    @keyup.enter="connect"
                    :append-icon="showPassword ? 'visibility' : 'visibility_off'"
                    :append-icon-cb="() => (showPassword = !showPassword)"
                    :type="showPassword ? 'password' : 'text'"
                    label="Κωδικός"
                    v-model="password"
                    color="light-green"
                    single-line
                    prepend-icon="security"
                    hint="Κωδικός διαχειριστή με αλφαριθμητικούς χαρακτήρες."></v-text-field>
                </v-list-tile-content>
              </v-list-tile>
            </v-list>
          </v-form>
        </v-card-text>
        <v-card-actions class="pa-0">
          <v-btn flat large class="ma-0" block @click="connect" dark color="light-green">ΣΥΝΔΕΣΗ</v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </CardLayout>
</template>

<script>
import AdminEdit from '@/components/editActions/AdminEdit'
import CardLayout from '@/components/layouts/CardLayout'
import swal from 'sweetalert2'
export default {
  name: 'Edit',
  components: {
    AdminEdit,
    CardLayout
  },
  data () {
    return {
      admin: false,
      username: '',
      password: '',
      showPassword: true
    }
  },
  created () {
    this.admin = (sessionStorage.getItem('admin') !== null)
  },
  methods: {
    connect () {
      if (this.$refs.userForm.validate()) {
        if (this.username === 'admin' && this.password === '309048') {
          sessionStorage.setItem('admin', true)
          this.admin = true
        } else {
          swal({
            type: 'error',
            title: 'Σφάλμα σύνδεσης!',
            text: 'Λάθος Κωδικός ή Όνομα Χρήστη!'
          })
        }
      }
    }
  }
}
</script>

<style lang="css">
.nbr{
  border-radius: 0px;
}
</style>



