<template>
  <v-card-text class="pa-0">
    <v-list subheader class="pa-0">
      <v-divider></v-divider>
      <v-subheader :class="plant.vue_color" class="lighten-5">
        <v-icon class="mr-2">notes</v-icon>
        <v-spacer></v-spacer>
        <span>Κατάλογος Ασθενειών</span>
      </v-subheader>
      <v-divider></v-divider>
      <template v-for="(disease, i) in plant.diseases">
        <v-list-tile @click="showDisease(plant.gr, disease.name)" :key="`tile_${i}`">
          <v-list-tile-avatar>
            <v-icon :color="plant.vue_color">local_hospital</v-icon>
          </v-list-tile-avatar>
          <v-list-tile-content>
            <v-list-tile-title>{{ disease.name }}</v-list-tile-title>
            <v-list-tile-sub-title>
              <v-icon small :color="plant.vue_color + ' darken-4'">bug_report</v-icon>
              <em v-if="disease.parasitic">{{ disease.pathogen }}</em>
              <span v-else>{{ disease.pathogen }}</span>
            </v-list-tile-sub-title>
          </v-list-tile-content>
          <v-list-tile-action><v-icon>search</v-icon></v-list-tile-action>
        </v-list-tile>
        <v-divider v-if="i + 1 < plant.diseases.length"
          inset
          :class="plant.vue_color + ' darken-4'"
          :key="`divider_${i}`">
         </v-divider>
      </template>
    </v-list>
  </v-card-text>
</template>

<script>
import {bus} from '@/lib/bus'
export default {
  name: 'CatalogDiseaseList',
  props: ['plant'],
  data () {
    return {
    }
  },
  methods: {
    showDisease (plant, disease) {
      bus.$emit('conclusion', { done: false, steps: [] })
      this.$router.push(`/diagnosis/${plant}/${disease}`)
    }
  }
}
</script>

<style lang="css" scoped>
.cup{
  cursor: pointer;
}
.ntt{
  text-transform: none;
}
</style>



