<template>
  <v-list-group lazy :disabled="groupDisabled">
    <v-list-tile :disabled="groupDisabled" slot="activator">
      <v-list-tile-avatar>
        <v-icon medium :color="color">{{ icon }}</v-icon>
      </v-list-tile-avatar>
      <v-list-tile-content>
        <v-list-tile-title v-html="title"></v-list-tile-title>
        <v-list-tile-sub-title v-html="sub"></v-list-tile-sub-title>
      </v-list-tile-content>
    </v-list-tile>
    <v-divider></v-divider>
    <slot name="items"></slot>
  </v-list-group>
</template>

<script>
export default {
  name: 'ListGroup',
  props: ['icon', 'color', 'title', 'sub', 'groupDisabled'],
  data () {
    return {
    }
  }
}
</script>

<style lang="css">
</style>



