<template>
  <v-card>
    <v-card-media src="/static/fill/disease.jpg" height="300px"></v-card-media>
    <v-card-title class="d-block">
      <div class="headline">{{ item.disease }}</div>
      <span class="grey--text">{{plant.gr}}</span>
    </v-card-title>
    <v-subheader :class="plant.vue_color+' lighten-5'" class="justify-center"><v-icon medium>{{item.icon}}</v-icon></v-subheader>
    <v-divider></v-divider>
    <v-card-text class="taj" v-html="item.title"></v-card-text>
    <v-divider></v-divider>
    <v-card-actions class="pa-0">
      <v-btn large block :color="plant.vue_color" flat class="ma-0" @click="handler">ΚΛΕΙΣΙΜΟ</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'DiseaseDialog',
  props: ['plant', 'item', 'handler'],
  data () {
    return {}
  }
}
</script>

<style lang="css">
.taj{
  text-align: justify;
}
</style>



