<template>
  <v-list-tile @click="handler">
    <v-list-tile-avatar>
      <v-icon medium :color="color">{{ icon }}</v-icon>
    </v-list-tile-avatar>
    <v-list-tile-content>
      <v-list-tile-title>
        <slot></slot>
      </v-list-tile-title>
      <v-list-tile-sub-title v-html="sub"></v-list-tile-sub-title>
    </v-list-tile-content>
    <v-list-tile-action><v-icon>open_in_new</v-icon></v-list-tile-action>
  </v-list-tile>
</template>

<script>
export default {
  name: 'DiseaseTile',
  props: ['icon', 'color', 'sub', 'handler'],
  data () {
    return {

    }
  }
}
</script>

<style lang="css">
</style>



