<template>
  <v-card id="AddPlant" class="pa-0 ma-0">
    <v-card-text class="pa-0">
      <v-form ref="plantForm">
        <v-list two-line class="pa-0">
          <v-subheader class="subheading lime accent-4 justify-center">{{ subHeader }}</v-subheader>
          <v-divider class="light-green"></v-divider>
          <template v-for="(field, f) in fields">
            <v-divider :key="`topDivider-${f}`"></v-divider>
            <v-subheader :key="`subheader-${f}`" class="light-green lighten-5">{{ field.subHeader }}</v-subheader>
            <v-divider :key="`bottomDivider-${f}`"></v-divider>
            <v-list-tile :key="`tile-${f}`">
              <v-list-tile-content>
                <v-text-field
                  single-line
                  color="light-green"
                  :label="field.label"
                  :hint="field.hint"
                  v-model="plant[field.model]"
                  :loading="adding"
                  :prepend-icon="field.icon"
                  :rules="field.required ? notEmptyRules:[]"
                  @keyup.enter="add">
                </v-text-field>
              </v-list-tile-content>
            </v-list-tile>
          </template>
        </v-list>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-btn flat fab color="green darken-1" :loading="adding" @click="add">
        <v-icon medium>add</v-icon>
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn flat fab @click="clear">
        <v-icon medium>refresh</v-icon>
      </v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
import API from '@/lib/API'
import swal from 'sweetalert2'
import _ from 'lodash'
export default {
  name: 'AddPlant',
  props: ['retrievedPlants', 'subHeader'],
  data () {
    return {
      plants: this.retrievedPlants,
      plant: {
        gr: '',
        latin: '',
        changeDate: null,
        vue_color: '',
        diseases: []
      },
      loading: false,
      notEmptyRules: [
        v => !!v || 'Απαιτούμενο πεδίο!'
      ],
      adding: false
    }
  },
  mounted () {},
  computed: {
    exists () {
      return {
        gr: _.flatMap(this.plants, _.property('gr')).includes(this.plant.gr),
        latin: _.flatMap(this.plants, _.property('latin')).includes(this.plant.latin)
      }
    },
    fields () {
      return [{
        subHeader: 'Κοινή ονομασία κηπευτικού',
        icon: 'local_florist',
        label: 'Κοινή ονομασία',
        model: 'gr',
        required: true,
        hint: 'Συμπληρώστε το όνομα με ελληνικούς χαρακτήρες.'
      }, {
        subHeader: 'Επιστημονική ονομασία κηπευτικού',
        icon: 'school',
        label: 'Επιστημονική ονομασία',
        model: 'latin',
        required: true,
        hint: 'Συμπληρώστε το όνομα στα λατινικά.'
      }, {
        subHeader: 'Φυσιολογικό χρώμα φυτού',
        icon: 'color_lens',
        label: 'Χρώμα',
        model: 'vue_color',
        required: false,
        hint: 'Συμπληρώστε το χρώμα με μικρά αγγλικά.'
      }]
    }
  },
  methods: {
    add () {
      if (this.plant.gr.trim().length === 0 || this.plant.latin.trim().length === 0) {
        return swal({
          type: 'error',
          title: 'Κενό πεδίο!',
          html: 'Δεν επιτρέπεται η χρήση κενών πεδίων. Προσπαθήστε ξανά!'
        })
      }
      if (this.exists.gr || this.exists.latin) {
        let existingTerm = this.exists.gr ? this.plant.gr : this.plant.latin
        return swal({
          type: 'error',
          title: 'Σφάλμα προσθήκης',
          html: `Το φυτό "<b>${existingTerm}</b>" υπάρχει ήδη στο αρχείο...`
        })
      } else {
        this.adding = true
        API.addPlant(this.plant).then(plant => {
          this.adding = false
          if (plant) {
            swal({
              type: 'success',
              title: 'Επιτυχής προσθήκη!',
              html: `Το φυτό <b>${plant.gr}</b> προστέθηκε επιτυχώς στο αρχείο! Πατώντας <b>OK</b> θα μεταφερθείτε αυτόματα στη σελίδα επεξεργασίας για το κηπευτικό.`
            }).then(ok => {
              this.$router.push(`/edit/${plant.gr}`)
              this.$router.go()
            })
          } else {
            swal({
              type: 'error',
              title: 'Σφάλμα προσθήκης φυτού!',
              html: `Το φυτό <b>${plant.gr}</b> δεν προστέθηκε στο αρχείο!`
            })
          }
        }).catch(error => console.log(error))
      }
    },
    clear () { this.$refs.plantForm.reset() }
  }
}
</script>

<style lang="css">
</style>



