<template>
    <v-list-tile
        :disabled="hasNotDiseases"
        slot="activator"
        :href="href"
        :to="route">
        <v-list-tile-avatar>
            <v-icon :color="plant.vue_color" medium>{{ avatar }}</v-icon>
        </v-list-tile-avatar>
        <v-list-tile-content>
            <v-list-tile-title><slot name="title"></slot></v-list-tile-title>
            <v-list-tile-sub-title>{{sub}}</v-list-tile-sub-title>
        </v-list-tile-content>
        <v-list-tile-action v-if="action">
            <v-icon>{{ icon || 'keyboard_arrow_right' }}</v-icon>
        </v-list-tile-action>
    </v-list-tile>
</template>

<script>
export default {
  name: 'PlantTile',
  props: ['avatar', 'action', 'plant', 'route', 'href', 'sub', 'hasNotDiseases', 'icon'],
  data () {
    return {

    }
  }
}
</script>



