import _ from 'lodash'
// import swal from 'sweetalert2'
/*
  Variables Table
  D_ARRAY = Diseases Array parameter
  S_ARRAY = Selected Symptoms Array parameter
  V_ARRAY = Selected Values Array parameter
*/
export default {
  // Symptom Based Possible Diseases
  S_BASED (D_ARRAY, S_ARRAY) {
    return D_ARRAY.filter(disease => {
      var isPossible = true // Disease possible index
      for (var selectedSymptom of S_ARRAY) {
        // Έστω κι ένα σύμπτωμα να μην υπάρχει η ασθένεια ΑΠΟΚΛΕΙΕΤΑΙ
        if (!_.keys(disease.rules).includes(selectedSymptom)) isPossible = false
      }
      return isPossible // Return only possible diseases
    }) // Return filtered diseases for use directly from function
  },
  // Value Based Possible Diseases
  V_BASED (D_ARRAY, I_O) {
    return D_ARRAY.filter(disease => {
      var isPossible = true
      for (var symptom of _.keys(disease.rules)) {
        if (I_O.SYMPTOMS.includes(symptom)) {
          for (var value of I_O.VALUES) {
            var qualifier = disease.rules[symptom][I_O.QUALIFIER]
            if (qualifier && !qualifier.includes(value) && !I_O.FALSY) { isPossible = false }
          }
        }
      }
      return isPossible
    }) // Return filtered diseases for use directly from function
  },
  PREVIEW (D_ARRAY) {
    var CARD = { value: true, buttonText: 'ΕΠΟΜΕΝΟ' }
    if (D_ARRAY.length === 0) {
      CARD.type = 'error'
      CARD.color = 'red'
      CARD.icon = 'not_interested'
      CARD.message = 'Καμία πιθανή ασθένεια με το συνδυασμό.'
    } else if (D_ARRAY.length === 1) {
      CARD.type = 'success'
      CARD.color = 'green'
      CARD.icon = 'assignment_turned_in'
      CARD.message = `Διαγνώσθηκε ασθένεια με αυτά τα κριτήρια.`
      CARD.buttonText = D_ARRAY[0].name
    } else {
      CARD.type = 'info'
      CARD.color = 'teal'
      CARD.icon = 'notes'
      CARD.message = `${D_ARRAY.length} πιθανές ασθένειες με το συνδυασμό.`
    }
    return CARD
  },
  STEPS (D_ARRAY, S_ARRAY) {
    var STEPS = []
    D_ARRAY.forEach(disease => {
      S_ARRAY.forEach(symptom => {
        _.forOwn(disease.rules[symptom], (values, qualifier) => {
          var step = { qualifier: qualifier, disabled: true, selectedValues: [] }
          if (STEPS.find(s => s.qualifier === qualifier) === undefined) {
            step.values = values
            STEPS.push(step)
          } else {
            STEPS.find(s => s.qualifier === qualifier).values =
            _.union(STEPS.find(s => s.qualifier === qualifier).values, values)
          }
        })
      })
    })
    return STEPS
  }
}



