<template>
  <v-card>
    <v-card-media src="/static/fill/values.jpg" height="300px"></v-card-media>
    <v-card-title class="d-block">
      <div class="headline">
        <v-badge :color="plant.vue_color">
          <span slot="badge">{{step.diseases.length}}</span>
          <span>{{ plant.gr }}</span>
        </v-badge>
      </div>
      <span class="grey--text">{{ step.qualifier }}<v-icon>arrow_right</v-icon>{{ step.value }}</span>
    </v-card-title>
    <v-divider></v-divider>
    <v-subheader :class="plant.vue_color+' lighten-5'">{{step.diseases.length}} ασθένειες με την τιμή</v-subheader>
    <v-divider></v-divider>
    <v-card-text class="pa-0">
      <v-list class="pa-0">
        <template v-for="(disease, d) in step.diseases">
          <v-list-tile :key="`diseaseTile-${d}`">
            <v-list-tile-avatar>
              <v-icon>local_hospital</v-icon>
            </v-list-tile-avatar>
            <v-list-tile-content>{{ disease }}</v-list-tile-content>
          </v-list-tile>
          <v-divider :inset="d < step.diseases.length-1" :key="`diseaseDivider-${d}`"></v-divider>
        </template>
      </v-list>
    </v-card-text>
    <v-card-actions class="pa-0">
      <v-btn large block :color="plant.vue_color" flat class="ma-0" @click="handler">ΚΛΕΙΣΙΜΟ</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
export default {
  name: 'ValueDialog',
  props: ['plant', 'step', 'handler'],
  data () {
    return {}
  }
}
</script>

<style lang="css">
</style>



